<template>
  <div id="app">
    <template v-if="isLoggedIn">
      <MenuBarComponent/>
      <div class="admin-panel-choice-menu"  v-if="(userInfo.role == 'admin' || userInfo.role == 'manager')">
        <div class="choise-block"> <span>Вікна:</span> </div>
        <div class="choise-block button-choice" @click="openedWindow = 'manager'" :class="openedWindow == 'manager' ? 'is-active':''"><span>Працівники</span> </div>
        <div class="choise-block button-choice" @click="openedWindow = 'coordinator'" :class="openedWindow == 'coordinator' ? 'is-active':''"><span>Техніка</span></div>
        <div v-if="userInfo.role == 'admin'"  class="choise-block button-choice" @click="openedWindow = 'repairman'" :class="openedWindow == 'repairman' ? 'is-active':''" ><span>Ремонт</span></div>
      </div>
      <UsersManagement v-if="openedWindow == 'manager'" />
      <RepairCoordinatorPanel v-if="openedWindow == 'coordinator' || userInfo.role == 'coordinator'"/>
      <RepairmanPanel v-if="openedWindow == 'repairman' || userInfo.role == 'repairman'"/>
      <div class="user-is-not-verify-box">
        <span class="user-is-not-verify" v-if="getAccountIsNotAcceptedAlert">{{ getAccountIsNotAcceptedAlert }}</span>
      </div>
    </template>
    <AuthorizationAndRegistration v-if="!isLoggedIn"/>
  </div>
</template>

<script>
import AuthorizationAndRegistration from "./components/AuthorizationAndRegistration.vue";
import MenuBarComponent from "./components/MenuBarComponent.vue";
import UsersManagement from "./components/UsersManagement.vue";
import RepairCoordinatorPanel from "./components/RepairCoordinatorPanel.vue";
import RepairmanPanel from "./components/RepairmanPanel.vue";

export default {
  name: "App",

  components: {
    AuthorizationAndRegistration,
    MenuBarComponent,
    UsersManagement,
    RepairCoordinatorPanel,
    RepairmanPanel,
  },

  data() {
    return {
      openedWindow: '',
      accountIsNotAcceptedAlert: '',
    };
  },

  methods: {
    clearData(){
      this.openedWindow = '';
      this.accountIsNotAcceptedAlert = '';
    },
    
  },

  computed: {
    isLoggedIn() {
      return this.$store.getters['isLoggedIn'];
    },
    userInfo() {
      return this.$store.getters['getUserInfo'];
    },
    getAccountIsNotAcceptedAlert() {
      return this.userInfo && !this.userInfo.role ? 'Будь-ласка, зачекайте поки ваш аккаунт підтвердять' : '';
    },
    
  },

  mounted() { 
    this.clearData();
    if(localStorage.getItem('uid')){
      this.$store.dispatch('setAccountInfo', localStorage.getItem('uid')).then(() => {
        this.openedWindow = (this.userInfo && (this.userInfo.role == 'admin' || this.userInfo.role == 'manager')) ? 'manager' : '';
      });
    }
    
  },
};
</script>

<style lang="less">
  @import "./assets/less/index.less";
</style>