<template>
    <div class="menu-container">
        <div class="user-menu-container">
            <span class="company-name">Belogvi</span>
            <div class="user-name-icon-container" @click="toggleUserIcons()">
                <span class="user-fio">{{ (this.userInfo.lastname ? this.userInfo.lastname + ' ' : '') +
                    (this.userInfo.firstname ? this.userInfo.firstname : '') }}</span>
                <div class="user-profile-button">
                    <img v-if="!isMouseOver" src="../assets/images/user-icon-img.svg">
                    <img v-if="isMouseOver" src="../assets/images/user-icon-img-active.svg">
                </div>
                <div v-if="isProfileMenuVisible" class="menu-choice-container"
                    :class="isProfileMenuVisible ? 'v-enter' : ''">
                    <ul class="profile-menu">
                        <li v-if="userInfo.role != 'driver'" @click="openCloseProfile()">{{ userProfileIsOpened ?
                            'Повернутись' : 'Профіль' }}</li>
                        <li class="end-session" @click="logout()">Вийти з акаунту</li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="user-profile-wrapper" v-if="userProfileIsOpened || userInfo.role == 'driver'">
            <h2 class="profile-title">Ваш профіль</h2>
            <div class="user-info-box" v-if="!editUserInfoWindowIsOpened">
                <div class="user-info-container">
                    <h2 class="user-name">{{ getUserFullName() }}</h2>
                    <div class="user-info-text">
                        <span class="information-title"> Пошта:</span>
                        <span class="user-information"> {{ this.userInfo.email }}</span>
                    </div>
                    <div class="user-info-text">
                        <span class="information-title"> Номер:</span>
                        <span class="user-information"> {{ this.userInfo.phoneNumber ? this.userInfo.phoneNumber : 'Не вказаний' }}</span>
                    </div>
                    <div class="user-info-text">
                        <span class="information-title"> Посада:</span>
                        <span class="user-information"> {{ displayUserRole() }}</span>
                    </div>
                </div>
                <div class="control-buttons-container">
                    <button class="control-button" @click="openEditUserProfile">Редагувати</button>
                    <button class="control-button" v-if="userInfo.role != 'driver'"
                        @click="userProfileIsOpened = false">Назад</button>
                </div>
            </div>
            <div class="driver-car-info-container" v-if="(userInfo.role == 'driver') && userInfo.car">
                <h2 class="profile-title">Ваша машина</h2>
                <div class="car-info-container">
                    <div class="car-info-text">
                        <span class="car-information"> {{ allCars[userInfo.car]?.model }}</span>
                    </div>
                    <div class="car-info-text">
                        <span class="information-title"> Ліцензійний номер:</span>
                        <span class="car-information"> {{ allCars[userInfo.car]?.licensePlate }}</span>
                    </div>
                    <div class="car-info-text">
                        <span class="base-information"> {{  displayCarState }}</span>
                    </div>
                </div>
                
            </div>


            <div class="edit-profile-menu-container" v-if="editUserInfoWindowIsOpened">
                <h2>Вікно редагування профілю</h2>
                <div class="fio-box-inputs-container">
                    <div class="first-name-box">
                        <input type="text" id="first-name" placeholder="Ім'я *" v-model="newUserData.firstname"
                            :class="{ 'invalid-register-inputs-true': !isValidName(newUserData.firstname) && (newUserData.firstname || emptyInputData) }">
                        <label for="first-name" class="invalid-register-inputs"
                            v-if="!isValidName(newUserData.firstname) && (newUserData.firstname || emptyInputData)">
                            {{ emptyInputData && !newUserData.firstname ? "Заповніть поле" : "Некорректно" }}
                        </label>
                    </div>
                    <div class="last-name-box">
                        <input type="text" id="last-name" placeholder="Прізвище" v-model="newUserData.lastname"
                            :class="{ 'invalid-register-inputs-true': !isValidName(newUserData.lastname) && newUserData.lastname }">
                        <label for="last-name" class="invalid-register-inputs"
                            v-if="!isValidName(newUserData.lastname) && newUserData.lastname">
                            Некорректно
                        </label>
                    </div>
                    <div class="surname-box">
                        <input type="text" id="surname" placeholder="По-батькові" v-model="newUserData.surname"
                            :class="{ 'invalid-register-inputs-true': !isValidName(newUserData.surname) && newUserData.surname }">
                        <label for="surname" class="invalid-register-inputs"
                            v-if="!isValidName(newUserData.surname) && newUserData.surname">
                            Некорректно
                        </label>
                    </div>
                </div>

                <div class="phone-number-container">
                    <div class="ukraine-number-code">
                        <span>{{ phoneRegion }}</span>
                    </div>
                    <input type="tel" v-model="newUserData.phoneNumber"
                        :class="{ 'invalid-register-inputs-true': newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length && newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length < 9 }"
                        id="phone" placeholder="(__) ___-__-__" autocomplete="tel" v-phone />
                </div>

                <label for="phone" class="invalid-register-inputs"
                    v-if="newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length && newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length < 9 && editUserInfoWindowIsOpened">
                    Номер неповний
                </label>

                <div class="edit-profile-buttons-container">
                    <button class="edit-profile-button" @click="saveEditedUserProfile">Зберегти зміни</button>
                    <button class="edit-profile-button" @click="editUserInfoWindowIsOpened = false">Назад</button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
const roleMappings = {
    'admin': 'Адміністратор',
    'manager': 'Менеджер',
    'repairman': 'Ремонтник',
    'coordinator': 'Координатор',
    'driver': 'Водій',
};
const carStates = {
    'inUse': 'В робочому стані',
    'sentToRepair': 'Потрібно відправитися на ремонт',
    'beingRepaired': 'Ремонтується',
}
import PhoneDirective from '../directives/PhoneDirective';
export default {
    name: 'MenuBar',

    props: {

    },
    directives: {
        phone: PhoneDirective,
    },
    data() {
        return {
            isProfileMenuVisible: false,
            isMouseOver: false,
            userInfo: '',
            userProfileIsOpened: false,
            emptyInputData: false,
            editUserInfoWindowIsOpened: '',
            phoneRegion: '+380 ',
            newUserData: {
                phoneNumber: '',
                firstname: '',
                lastname: '',
                surname: '',
            },
            carInfo: '',
            repairBaseInfo: '',
            allCars: {},
            allRepairBases: {},
        };
    },

    methods: {
        toggleUserIcons() {
            this.isMouseOver = !this.isMouseOver;
            this.isProfileMenuVisible = !this.isProfileMenuVisible;
        },
        logout() {
            this.$store.dispatch('logout');
        },
        getUserFullName() {
            console.log()
            return (this.userInfo.lastname ? this.userInfo.lastname + ' ' : '') + this.userInfo.firstname + (this.userInfo.surname ? ' ' + this.userInfo.surname : '');
        },
        openCloseProfile() {
            if (this.userProfileIsOpened) {
                this.userProfileIsOpened = false;
            } else {
                this.userProfileIsOpened = true;
            }
        },
        displayUserRole() {
            return this.userInfo.role ? roleMappings[this.userInfo.role] : 'Очікує підтвердження';
        },
        isValidName(name) {
            return /^[a-zA-Zа-яА-ЯёЁґҐіІїЇєЄ' -]+$/.test(name);
        },
        replaceUnderscoresWithNumbers(placeholder, number) {
            const numberWithoutRegion = number.replace(this.phoneRegion, '').replace(/[_\-)(\s]/g, '');
            let result = placeholder;
            let index = 0;
            for (const char of placeholder) {
                if (char === '_') {
                    result = result.replace('_', numberWithoutRegion[index]);
                    index++;
                }
            }
            return result;
        },
        openEditUserProfile() {
            this.editUserInfoWindowIsOpened = true;
            if (this.userInfo.phoneNumber.length) {
                console.log(this.userInfo.phoneNumber);
                this.newUserData.phoneNumber = this.replaceUnderscoresWithNumbers('(__) ___-__-__', this.userInfo.phoneNumber);
            } else {
                this.newUserData.phoneNumber = '(__) ___-__-__';
            }

            this.newUserData.firstname = this.userInfo.firstname;
            this.newUserData.lastname = this.userInfo.lastname;
            this.newUserData.surname = this.userInfo.surname;
        },
        saveEditedUserProfile() {
            if (((this.newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length == 9 && this.newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length) || !this.newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length)
                && (this.isValidName(this.newUserData.firstname) && (this.isValidName(this.newUserData.lastname) || !this.newUserData.lastname) && (this.isValidName(this.newUserData.surname) || !this.newUserData.surname))) {
                this.newUserData.phoneNumber = this.newUserData.phoneNumber.replace(/[_\-)(\s]/g, '').length ? (this.phoneRegion + this.newUserData.phoneNumber) : '';
                this.userInfo.firstname = this.newUserData.firstname;
                this.userInfo.lastname = this.newUserData.lastname;
                this.userInfo.surname = this.newUserData.surname;
                this.userInfo.phoneNumber = this.newUserData.phoneNumber ? this.newUserData.phoneNumber : '';
                this.$store.dispatch('updateUserAccount', { newData: this.newUserData, uid: this.userInfo.uid });
                this.editUserInfoWindowIsOpened = false;
            } else {
                this.emptyInputData = true;
            }
        },
        fetchRepairBases(){
        this.$delete(this.allRepairBases);
        this.$store.dispatch('getAllRepairBases').then((querySnapshot)=>{
            querySnapshot.forEach((result) => {
            var repairBase = result.data();
            this.$set(this.allRepairBases, result.id, {id: result.id, name: repairBase.name, adress: repairBase.adress});
            });
        });
        }

    },

    computed: {
        getUserInfo() {
            return this.$store.getters['getUserInfo'];
        },
        getCarInfo() {
            return this.$store.getters['getCarInfo'];
        },
        getRepairBaseInfo() {
            return this.$store.getters['getRepairBaseInfo'];
        },
        displayCarState() {
            const carState = this.allCars[this.userInfo.car]?.state;
            if (carState == 'sentToRepair') {
                return carStates[carState] + ' у базу ремонту ' + this.allRepairBases[this.allCars[this.userInfo.car].repairBase]?.name + " за адресою " + this.allRepairBases[this.allCars[this.userInfo.car].repairBase]?.adress;
            } else if (carState == 'beingRepaired') {
                return carStates[carState] + ' на базі ремонту ' + this.allRepairBases[this.allCars[this.userInfo.car].repairBase]?.name + " за адресою " + this.allRepairBases[this.allCars[this.userInfo.car].repairBase]?.adress;
            } else {
                return carStates[carState];
            }
        },
    },

    mounted() {
        if (localStorage.getItem('uid')) {
            this.$store.dispatch('setAccountInfo', localStorage.getItem('uid')).then(() => {
                this.userInfo = this.getUserInfo;
            });
        }
        this.$store.dispatch('getAllCars').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var car = result.data(); 
        this.$set(this.allCars, car.licensePlate, car);
      });
      
    });
        this.fetchRepairBases()
            
        


    },
}
</script>